<template>
  <div class="page" :class="{page_selected:selected}">
    <div class="page_head" @click="handleClick">
      <div class="page_icon" :style="{color}">
        <svg-icon :iconClass='page.icon'></svg-icon>
      </div>
      <div class="page_menu">
        <svg-icon iconClass='details'></svg-icon>
      </div>
    </div>
    <div class="page_body">
      <h3 class="page_title">{{page.name}}</h3>
      <!-- <p>{{page.description}}</p> -->
    </div>
  </div>
</template>

<script>
export default {
  name: "Page",
  props: {
    page: {
      type: Object,
      required: true
    },
    selected: {
      type: Boolean
    }
  },
  data() {
    return {};
  },
  components: {},
  mounted() {},
  computed: {
    color() {
      return this.page.colors[0];
    }
  },
  methods: {
    handleClick() {
      const appRect = document.querySelector("#app").getBoundingClientRect();
      const elRect = this.$el.getBoundingClientRect();
      const page = this.page;
      const rect = {};
      rect.top = elRect.top - appRect.top;
      rect.left = elRect.left - appRect.left;
      rect.width = elRect.width;
      rect.height = elRect.height;
      rect.appWidth = appRect.width;
      rect.appHeight = appRect.height;
      this.$emit("select", { rect, page });
    }
  }
};
</script>

<style lang='less' scoped>
.page {
  flex: 1;
  margin: 0 8px;
  overflow: hidden;
  box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
  color: #666;
  &.page_selected {
    visibility: hidden;
  }
  .page_head {
    display: flex;
    padding: 20px;
    height: 44px;
    justify-content: space-between;
    align-items: flex-start;
    transform: translate3d(0, 0, 0);
    will-change: transform;
    .page_icon {
      display: flex;
      width: 44px;
      height: 44px;
      border: 1px solid #eee;
      border-radius: 100%;
      justify-content: center;
      align-items: center;
      font-size: 18px;
    }
    .page_menu {
      color: #eee;
    }
  }
  .page_body {
  padding: 20px 20px;
  transform: translate3d(0, 189px, 0);
  will-change: transform;
  .page_title{
    margin-top: 6px;
    font-size: 32px;
    font-weight:100;
  }
}
}
</style>