<template>
  <div class="page-list" :class="{ 'page-list_selected':!!selected}">
    <ul :style="{width:`${resumes.length*100}%`}">
      <li
        v-for="item in resumes"
        :key="item.name"
        :style="{transform:`translate3d(-${currentIndex*100}%,0,0)`}"
      >
        <page :page="item" :selected="selected&& item ===selected.page" @select="selectPage"></page>
      </li>
    </ul>
  </div>
</template>

<script>
import Page from "./Page";
import { mapState, mapMutations } from "vuex";
export default {
  name: "PageList",
  data() {
    return {};
  },
  components: {
    Page
  },
  computed: {
    ...mapState(["resumes", "currentIndex", "selected"])
  },
  mounted() {
    let touch = {};
    this.$el.addEventListener("touchstart", evt => {
      touch.startX = evt.touches[0].clientX;
      touch.endX = 0;
      console.log("X" + "" + touch.startX);
    });
    this.$el.addEventListener("touchmove", evt => {
      touch.endX = evt.touches[0].clientX;
      console.log(touch.endX);
    });
    this.$el.addEventListener("touchend", () => {
      if (!touch.endX || Math.abs(touch.endX - touch.startX) < 10) {
        return;
      }
      if (touch.endX < touch.startX) {
        this.nextPage();
      } else {
        this.prevPage();
      }
    });
  },
  methods: {
    ...mapMutations(["selectPage", "nextPage", "prevPage"])
  }
};
</script>

<style lang='less' scoped>
.page-list {
  padding: 0 32px;
  height: 400px;
  transition: all 0.5s ease;
  > ul, > ul > li {
    display: flex;
    height: 100%;
  }
  > ul > li {
    list-style: none;
    flex: 1;
    transition: transform 0.5s ease;
    .page {
      border-radius: 8px;
      background-color: white;
    }
  }
}

.page-list_selected {
  transform: scaleX(1.25);
}
</style>