<template>
  <transition name="show" @enter="handleEnter" @leave="handleLeave">
    <div class="page-detail" v-if="selected">
      <nav-bar @left="unselectPage" :title="selected.page.name"></nav-bar>
      <!-- <page :page="selected.page" :active="true"></page> -->
      <div v-if="selected.page.name==='personal'">
        <Personal></Personal>
      </div>
      <div v-else-if="selected.page.name==='project'">
        <Project></Project>
      </div>
      <div v-else>
        <Experience></Experience>
      </div>
    </div>
  </transition>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import NavBar from "./NavBar";
import Page from "./Page";
import Personal from "./Personal";
import Project from "./Project";
import Experience from "./Experience";
export default {
  name: "PageDetail",
  data() {
    return {};
  },
  components: {
    NavBar,
    Page,
    Personal,
    Project,
    Experience
  },
  computed: {
    ...mapState(["selected", "unselect"])
  },
  methods: {
    ...mapMutations(["unselectPage"]),
    handleEnter(el) {
      Object.assign(el.style, {
        top: `${this.selected.rect.top}px`,
        left: `${this.selected.rect.left}px`,
        width: `${this.selected.rect.width}px`,
        height: `${this.selected.rect.height}px`
      });
      setTimeout(() => {
        Object.assign(el.style, {
          top: 0,
          left: 0,
          width: `${this.selected.rect.appWidth}px`,
          height: `${this.selected.rect.appHeight}px`
        });
      }, 0);
    },
    handleLeave(el) {
      Object.assign(el.style, {
        top: 0,
        left: 0,
        width: `${this.unselect.rect.appWidth}px`,
        height: `${this.unselect.rect.appHeight}px`
      });
      setTimeout(() => {
        Object.assign(el.style, {
          top: `${this.unselect.rect.top}px`,
          left: `${this.unselect.rect.left}px`,
          width: `${this.unselect.rect.width}px`,
          height: `${this.unselect.rect.height}px`
        });
      }, 0);
    }
  }
};
</script>

<style lang='less' scoped>
.page-detail {
  position: fixed;
  display: flex;
  flex-direction: column;
  border-radius: 0;
  background-color: white;
  color: #666;
  will-change: top, left, width, height;
  .page {
    margin: 0;
    margin-top: -44px;
    padding: 0 20px;
    box-shadow: none;
  }
  .page_head,
  .page_body {
    transform: translate3d(0, 88px, 0);
  }
  .page_menu {
    opacity: 0;
  }
  .nav-bar {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
.show-enter-to,
.show-leave {
  border-radius: 0;
  .page {
    padding: 0 20px;
  }
  .page_head,
  .page_body {
    transform: translate3d(0, 88px, 0);
  }
  .page_menu {
    opacity: 0;
  }

  .nav-bar {
    opacity: 1;
    transform: translate3d(0, 0, 0);
  }
}
.show-leave-to,
.show-enter {
  border-radius: 8px;
  .page {
    padding: 0;
  }
  .page_head {
    transform: translate3d(0, 0, 0);
  }
  .page_body {
    transform: translate3d(0, 189px, 0);
  }
  .page_menu {
    opacity: 1;
  }
  .nav-bar {
    opacity: 0;
    transform: translate3d(0, -100%, 0);
  }
}
.show-enter-active,
.show-leave-active {
  transition: all 0.5s ease;
  .page,
  .page_head,
  .page_body,
  .page_menu,
  .nav-bar {
    transition: all 0.5s ease;
  }
}
</style>
