<template>
  <div class="gradient_color" :class="{gradient_color_active:active}" :style="{backgroundImage:gradientColor}"></div>
</template>

<script>
export default {
  name: "GradientColor",
  props:{
    colors:{
      type:Array
    },
    active:{
      type:Boolean
    }
  },
  data() {
    return {};
  },
  computed:{
      gradientColor () {
      return `linear-gradient( ${this.colors[1]}, ${this.colors[0]})`
    }
  },
  mounted() {},
  methods: {}
};
</script>

<style lang='less' scoped>
.gradient_color{
  position: absolute;
  width: 100%;
  height: 100%;
  opacity:0;
  transition:opacity 0.5s ease;
  &.gradient_color_active{
    opacity:1;
  }
}
</style>