<template>
   <div class="gradient">
     <gradient-color v-for='(item,index) in resumes' :key='item.name' :colors='item.colors' :active='index <= currentIndex'></gradient-color>
   </div>
</template>

<script>
import {mapState} from 'vuex'
import GradientColor from './GradientColor'

export default {
  name: 'Gradient',
  data () {
    return {
       }
  },
  components: {
    GradientColor
  },
  computed:{
    ...mapState(['resumes','currentIndex'])
  },
  methods: {}
}
</script>

<style lang='less' scoped>
.gradient{
  position: fixed;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
}
</style>