import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    selected: null,
    unselect: null,
    currentIndex:0,
    resumes: [
      {
        icon: 'user',
        name: 'personal',
        colors: ['#ff6262', '#ffa947'],
        description: '邮箱：18801054120@163.com'
      },
      {
        icon: 'project',
        name: 'project',
        colors: ['#5b9df9', '#47bfff'],
        description:'github: https://shaoxinghua.github.io/resume/'
      },
      {
        icon: 'experience',
        name: 'experience',
        colors: ['#2c7d59', '#3ba776'],
        description: 'takes work ethics very seriously. I do what I am paid for, and I do it well'
      }
    ],
  },
  mutations: {
    selectPage (state, selected) {
      state.unselect = null
      state.selected = selected
      
    },
    unselectPage(state) {
      state.unselect = state.selected
      state.selected = null
    },
    nextPage (state) {
      if (state.currentIndex < state.resumes.length - 1) {
        state.currentIndex++
      }
    },
    prevPage (state) {
      if (state.currentIndex > 0) {
        state.currentIndex--
      }
    },
  },
  actions: {
  },
  modules: {
  }
})
